body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}
body{
  height: 100vh;
  width: 100vw;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #303136;
  height: 100%;
  width: 100%;
  align-items: center;
  justify-content: center;
  color: white;
}

.nav-pills>a:hover {
  background-color: #545454;
  color: #d6d6d6;
  border-radius: 0%
}

.nav-pills>a.nav-link.active {
  background-color: #545454;
  color: white;
  border-radius: 0%
}

.nav-pills>a {
  color: #b1b1b1;
}

.nav-pills {
  background-color: #303136;
  color: white;
  font-size: 100%;
}

.bottomRow{
  height: calc(100% - 25px);
}

.sidebar{
  background-color:#303136;
  min-width:200px;
  height: 100%;
}

hr.titleLine{
  border:1px solid rgba(187, 187, 187, 0.1) ;
  width: 95%;
  border-top-right-radius: 1px;
  border-bottom-right-radius: 1px;
}

.logoSide{
  width: 93%;
  margin: 0 auto;
}
.map{
  height: 70vh;
}

.list{
  background-color: #363940;
  height: 100%;
  display: flex;
  flex-direction: column;
  max-width: none;
}

.listChild{
  background-color: #3b3f46;
  height: 100%;
  display: flex;
  flex-direction: column;
  max-width: none;
}
.nav-reportList>a:hover {
  background-color: #545454;
  color: #d6d6d6;
  border-radius: 0%
}

.nav-reportList{
  margin-right: 13px;
}

.nav-reportList>a.nav-link.active {
  background-color: #545454;
  color: white;
  border-radius: 0%
}


.reportFont{
  color: white !important;
  display: flex !important;
  justify-content: flex-start;
   align-items: center;
}

.reportFont light{
  color:#b1b1b1 ;
  font-size: 12px;
}

.reportDetails{
  color: white !important;
}

.reportDetails light{
  color:#b1b1b1 ;
  font-size: 12px;
}
.reportDesc light{
  color:#b1b1b1 ;
  font-size: 16px;
}
.reportDesc{
  height: 53%;
  padding: 10px;
  padding-right: 20px;
  background-color: rgba(255,255,255,0.1);
  margin-bottom:10px;
  border: 0px solid rgba(187, 187, 187, 0.1) ;
  border-radius: 10px;
  border-right-width: 0px;
  border-bottom-right-radius: 0px;
  border-top-right-radius: 0px;
}

.listChild>.simplebar-track.simplebar-vertical{
  top: 3px;
  right: 2px;
  bottom: 3px;
  width: 10px;
  background-color: #484a50;
}
.stats{
  background-color: #363940;
  border-bottom-style: solid;
  border-color: rgba(0, 0, 0, 0.3);
  border-bottom-width: 1px;
  
}

.simplebar-scrollbar:before {
  background-color: #0c0c0c;

}

.simplebar-scrollbar {
  right: -10px;
  width: 25px;
}

.simplebar-track.simplebar-vertical {
  top: 3px;
  right: 2px;
  bottom: 3px;
  width: 10px;
  background-color: #313236;
  
  visibility: visible !important;
}

.App-link {
  color: #61dafb;
}

.hideInfo{
  text-align: center;
  cursor: pointer;
  width: 25px;
  height: 25px;
  margin: 10px;
  margin-left: 1px;
}

.hideInfo:hover {
  background-color: #545454;
  border-radius: 100%;
}

.deleteEmergencyButton{
  text-align: center;
  cursor: pointer;
  color: #b1b1b1 !important;
  background-color:transparent;
  border: none;
  font-size: 18px;
}

.deleteEmergencyButton:hover {
 color: white !important;
}


@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



.title-bar {
    -webkit-app-region: drag;
    margin: 0;
    display: flex;
    background-color: #1b1d1d;
    width: 100%;
    height: 25px;
    color: #b1b1b1;
  }
  
  .app-name-container {
    display: flex;
    justify-content: flex-start;
    padding-left: 10px;
    align-items: center;
    flex-grow: 1;
  }
  
  .window-controls-container {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  
.minimize-button {
    background-color:transparent;
    -webkit-app-region: no-drag;
    width: 30px;
    height: 25px;
    border: none;
    fill: #838383;
    text-align: center;
    cursor: pointer;
  }
  
  .minimize-button:hover {
    background-color: #545454;
    fill: white;
  }
  
  .min-max-button {
    background-color:transparent;
    -webkit-app-region: no-drag;
    width: 30px;
    height: 25px;
    border: none;
    fill: #838383;
    text-align: center;
    cursor: pointer;
  }
  
  .min-max-button:hover {
    background-color: #545454;
    fill: white;
  }
  
  .close-button {
    background-color:transparent;
    -webkit-app-region: no-drag;
    width: 30px;
    border: none;
    height: 25px;
    cursor: pointer;
    fill: #838383;
    text-align: center;
  }
  
  .close-button:hover {
    background-color: red;
    fill: white;
  }
